.flex_H {
    display: flex;
    flex-direction: row;
}

.flex_V {
    display: flex;
    flex-direction: column;
}

.flex_MV {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
}

.flex_MH {
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    align-items: center;
}

.flex_SH {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
}

.flex_SV {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
}

.flex_RH {
    display: flex;
    flex-direction: row;
    justify-content: right;
    text-align: right;
}

.flex_LH {
    display: flex;
    flex-direction: row;
    justify-content: left;
    text-align: left;
}

.flex_RV {
    display: flex;
    flex-direction: column;
    justify-content: right;
    text-align: right;
}

.flex_LV {
    display: flex;
    flex-direction: column;
    justify-content: left;
    text-align: left;
}

.flex_textbox {
    display: flex;
    background-color: rgb(227, 227, 227);
    border-radius: 15px;
    margin: 5px;
    max-height: 40px;
    padding: 5px;
    padding-right: 15px;
    padding-left: 15px;
    text-align: center;
}

.wrap {flex-wrap: wrap;}
.wrap_reverse {flex-wrap: wrap-reverse;}

.header_embed {
    margin: 0px;
    width: 100%;
    position: absolute; 
    height: 67.4px; 
    top: 0;
}

.footer_embed {
    margin: 0px;
    width: 100%;
    height: 100px;  /* absolute height, otherwise embed defaults to ~150 regardless of contents (???) */
}

html, body {
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
    overflow-y: auto;
} 